@import '../styles/mixins.scss';

.form-control{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: .25rem;

    form{
        position: relative;
        width: 90%;
        height: 90%;
        @include flex-row;
        padding: 1rem;
        background-color: #F5F5F5;
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        

        .image-container{
            position: relative;
            max-width: 30%;
            margin-left: 3rem;
            height: 100%;
            @include flex-col;
            align-items: center;
            gap: 2rem;
            border-right: 2px solid;

            img{
                width: 100px;
                height: 100px;
            }
        }

        .form-control{
            position: relative;
            margin: 0;
            padding: 0;
            width: 80%;
            height: 100%;
            @include flex-col;
            justify-content: center;
            align-items: center;
            

            // .toggle{
