            //         width: 7.5rem;
            //         border: 1px solid;
            //         padding: .25rem .5rem;
            //         background-color: #ddd;
            //         color: aqua;
            //         z-index: 1;

            //         &::before{
            //             position: absolute;
            //             content: '';
            //             left: 0;
            //             top: 0;
            //             width: 100%;
            //             height: 100%;
            //             background-color: #232323;
            //             z-index: -1;
            //             transform: scaleX(0);
            //             transform-origin: right;
