            //             transform-origin: left;
            //         }
            //         &:hover::before{
            //             transform-origin: left;
            //         }
            //     }
            // }

            .input-container{
                position: relative;
                width: 80%;
                height: 90%;
                @include flex-col;
                flex-wrap: wrap;
                gap: 2rem;
                padding: 1rem;
                overflow: hidden;
                
    
                .input-box{
                    position: relative;
                    width: 40%;
                    height: 4rem;
                    display: flex;
                    align-items: center;
                    padding: 1rem;
    
                    label{
                        position: absolute;
                        color: #232323;
                        font-size: 1rem;
                        transition: transform .3s ease-in-out;
                        @include flex-row();

                        p{
                            color: red;
                        }
                    }
                    
                    input{
                        background-color: transparent;
                        width: 80%;
                        height: 2rem;
                        border: 0;
                        border-bottom: 2px solid;  
                        z-index: 1;
                        font-size: .75rem;
                        padding: .2rem;
    
                        &:focus + label,
                        &:not(:placeholder-shown) + label {
                            color: #5a79a2;
                            transform: translate(-.4rem, -1.6rem) scale(.8);
                            transition: transform .3s ease-in-out;
                            border: 2px;
                        }
                    }
                }
    
                #dropdown{
                    display: flex;
                    flex-direction: row-reverse;
                    justify-content: flex-end;
                    gap: 1rem;
    
                    label{
                        position: relative;
                    }
    
                    select{
                        max-width: 7rem;
                        width: max-content;
                    }
                }
            }

            .btn-container{
                position: relative;
                width: 15rem;
                height: 2rem;
                display: flex;
                gap: 1rem;
            
                button{
                    width: 100%;
                    height: 100%;
                    background-color: transparent;
                    border: 0;
                    background-color: #AED2FF;
                    border-radius: .5rem;
                    cursor: pointer;
                }
            }

        }

    }

}