.contacts-container{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    border-radius: 5px;

    .all-contacts{
        position: relative;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        border-radius: 5px;

        nav{
            position: relative;
            width: 100%;
            height: 4rem;
            display: flex;
            align-items: center;
            gap: 10px;
            box-shadow: rgba(0, 0, 0, 0.1) 0px 10px 20px, rgba(0, 0, 0, 0.13) 0px 6px 6px;

            span{
                position: absolute;
                font-size: 1.5rem;
            }

            .searchBar{
                position: relative;
                width: 50%;
                display: flex;
                align-items: center;

                input{
                    width: 100%;
                    height: 2.5rem;
                    border-radius: 1rem;
                    transition: display 1s ease-in;
                    border: 0;
                    outline: none;
                    padding: 0 3rem;
                }

                #search-icon{
                    padding: 1rem;
                }

            }       

            #add-icon{
                display: flex;
                align-items: center;
                justify-content: center;
                width: 5rem;
                height: 2.5rem;
                right: 1rem;
                background-color: #8dc6ff;
                cursor: pointer;
            }
        }

        .details{
            position: relative;
            width: 100%;
            height: 2rem;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            padding: 1rem;
            gap: 1rem;
            cursor: pointer;
            user-select: none;
        }

        .contacts-wrapper{
            position: relative;
            overflow-y: scroll;
            width: 100%;
            padding: 1rem;

            table{
                position: relative;
                width: 100%;    
                
                th{
                    background-color: #8dc6ff;
                }

                th, td{
                    padding: 1rem;
                    font-size: larger;
                }

                td{
                    &:last-child{
                        position: relative;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        gap: 20px;

                        button{
                            position: relative;
                            width: 2.5rem;
                            height: 2.5rem;
                            border-radius: 5px;
                            z-index: 1;
                            transition: all 0.5s ease-in-out;

                            span{
                                width: 100%;
                                height: 100%;
                                display: flex;
                                align-items: center;
                                justify-content: center;
                            }
                       
                            &:before{
                                content: '';
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                transform: scaleX(0);
                                transform-origin: left;
                                transition: transform .5s ease-in-out;
                                z-index: -1;
                            }
                        }

                        #delete{
                            border: 1px solid red;
                            color: red;
                            
                            &::before{
                                background-color: red;
                            }

                            &:hover{
                                color: white;
                                transition: color 0.5s ease-in-out;
                            }

                            &:hover:before{
                                transform: scaleX(1);
                                transform-origin: right;
                                transition: transform .5s ease-in-out;
                            }
                        }

                        #update{
                            border: 1px solid green;
                            color: green;

                            &::before{
                                background-color: green;
                            }

                            &:hover{
                                color: white;
                                transition: color .5s ease-in-out;
                            }

                            &:hover:before{
                                transform: scaleX(1);
                                transform-origin: right;
                                transition: transform .5s ease-in-out;
                            }

                        }

                    }
                }

                tr{
                    &:nth-child(even){
                        background-color: #f2f2f2;
                    }
                }

            }
        }

    }
    .selected-contact{
        display: none;
        position: relative;
        width: 300px;
        border: 1px solid #ddd;
        flex-direction: column;

        .close{
            position: absolute;
            right: 1rem;
            cursor: pointer;
        }

    }

}