@import "./mixins.scss";

aside{
    position: relative;
    width: 100%;
    height: 100%;
    @include flex-col;
    padding: .5rem;
    gap: 1rem;
    background: $aside-bg-color;
    color: $aside-content-color;

    p{
        position: relative;
        width: 100%;
        height: 2rem;
        text-align: center;
        font-size: 1.5rem;
    }

    .menu-container{
        position: relative;
        width: 100%;
        height: calc(100% - 2rem);
        @include flex-col;
        gap: 1rem;
        padding: .5rem;
        font-size: 1rem;

        .menu-item{
            position: relative;
            width: 100%;
            height: 2rem;
            @include flex-row;
            justify-content: flex-start;
            align-items: center;
            gap: 1rem;
            padding: 1rem;

            .item-container{
                position: relative;
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                gap: .5rem;
            }
            
            .toggle-btn{
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                background: transparent;
                border: 0;
            }


            &::after{
                position: absolute;
                content: '';
                width: 10px;
                height: 100%;
                
                right: 0;
                transform: scaleX(0);
                transform-origin: right;
                transition: transform .2s ease-in-out;
            }

            &:hover{
                background-color: #E4F1FF;
                border-radius: 10px;
                overflow: hidden;
                color: #000;
            }

            &:hover::after{
                transform: scaleX(1.5);
                background-color: #75C2F6;
                transform-origin: right;
                transition: transform .2s ease-in-out;
            }
        }
    }

}